.container {
  background-color: rgba(238, 231, 231, 0.95);
  width: 59%;
  margin-left: auto;
  margin-right: auto;
  position: relative;
}
.container ul {
  list-style: none;
  padding: 0px;
}
.container .more {
  font-size: 180px;
  background-color: none;
  border: none;
  background: none;
  position: absolute;
  right: 0;
  left: 0;
  transform: translate(-300px, 175px) rotate(0deg);

  padding: 11px 37px;
  background-color: rgba(156, 209, 209, 0.7);

  border-radius: 80%;

  transition: all 300ms ease-in-out;
}

.more:hover {
  transform: translate(-300px, 175px) rotateZ(360deg);
}
.more:focus {
  outline: none;
}

.voters {
  background: none;
  font-size: 30px;
  border-color: yellow;
  background-color: yellowgreen;
  color: darksalmon;
  align-items: center;
}
.rating {
  font-size: 30px;
  border-radius: 50%;
  padding: 10px 18px;
  border: 2px solid;
}
.joke {
  font-size: 30px;
}
.joke-item {
  display: flex;
  align-items: center;
  justify-content: space-between;

  border-bottom: 2px solid black;
  padding: 10px;
}
.buttons {
  display: flex;
  align-items: center;
  justify-content: center;
}
.face {
  font-size: 80px;
  margin-right: 20px;
}
